.Tree {
    font-family: sans-serif;
    text-align: center;
}

.Tree .children {
    margin-left: 5%;
}

.submission {
    position: absolute;
    bottom: 1%;
    right: 1%;
    display: inline-block;
    outline: none;
    cursor: pointer;
    font-weight: 600;
    border-radius: 3px;
    padding: 12px 24px;
    border: 0;
    color: #3a4149;
    background: #e7ebee;
    line-height: 1.15;
    font-size: 16px;
}

.submission:hover {
    transition: all .1s ease;
    box-shadow: 0 0 0 0 #fff, 0 0 0 3px #1de9b6;
}



